	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Back in black","date":"05 Dec 2000","slug":"/2000-12-05-back-in-black/","tags":"Y2000"},"html":"<p>PapaScott (the website) is <a href=\"/1999/12/05\">one year old</a> today. Thanks to \"Userland\" for providing at no cost the <a href=\"http://www.editthispage.com\">webspace</a> and the software that makes it not only possible, but easy.</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/12/crh1203.jpg\" height=\"262\" width=\"350\" border=\"0\" alt=\"crh1203.jpg: \" /></p>\n<p>Our hosts Jörg and Susanne in Munich treated Christopher to a Bavarian breakfast: a fresh pretzel with butter and honey. It was amazing how all three could stick in his hair. And his face. And his clothes.</p>\n<p>Jörg insisted that Christopher roll up his sleeves for the true Bavarian look. At least he didn't insist on Lederhosen!</p>"},"next":{"fields":{"title":"Weekend Trip","date":"01 Dec 2000","slug":"/2000-12-01-weekend-trip/"}},"previous":{"fields":{"title":"Nursery School","date":"06 Dec 2000","slug":"/2000-12-06-nursery-school/"}}},{"node":{"fields":{"title":"Weekend Trip","date":"01 Dec 2000","slug":"/2000-12-01-weekend-trip/","tags":"Y2000"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/12/Travel.jpg\" height=\"467\" width=\"350\" border=\"0\" alt=\"Travel: \" /></p>\n<p>PapaScott, MamaMaus and Christopher are all packed up for their trip to Munich.</p>"},"next":{"fields":{"title":"Wir sind das Netz!","date":"30 Nov 2000","slug":"/2000-11-30-wir-sind-das-netz/"}},"previous":{"fields":{"title":"Back in black","date":"05 Dec 2000","slug":"/2000-12-05-back-in-black/"}}},{"node":{"fields":{"title":"Wir sind das Netz!","date":"30 Nov 2000","slug":"/2000-11-30-wir-sind-das-netz/","tags":"Y2000"},"html":"<p>In the very strange but true department: in front of our office today about 30 demonstrators protested against the Internet. The protest was aimed at the main tenants of our building, AOL Germany and Bertelsmann Online, with signs reading 'Where is our Internet' and chants of 'Wir sind das Netz' and 'Ihr seid drin, wir sind draußen' (a play on AOL Germany's ad campaign with Boris Becker, 'Bin ich drin?'). The best sign was 'Ficken statt Klicken', which can only be paraphrased as 'Make Love instead of Clicks.'</p>\n<p>Since the protesters also carried over half a dozen video cameras, so I am guessing that the protest was staged as part of some student film project. But it was fun nontheless.</p>"},"next":{"fields":{"title":"Flying away for the weekend","date":"28 Nov 2000","slug":"/2000-11-28-flying-away-for-the-weekend/"}},"previous":{"fields":{"title":"Weekend Trip","date":"01 Dec 2000","slug":"/2000-12-01-weekend-trip/"}}}],"pathPrefix":"","first":false,"last":false,"index":1032,"pageCount":1101}}

